// src/pages/HomePage.jsx

import { useState, useEffect } from "react";
import { Carousel } from "react-responsive-carousel";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import Header from "../components/Header";
import Footer from "../components/Footer";
import { Link } from "react-router-dom";
import { MessageSquare } from "lucide-react"; // Icon for the WhatsApp button

export default function HomePage() {
  const [products, setProducts] = useState([]);
  const [filteredProducts, setFilteredProducts] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState("all");
  const [categories, setCategories] = useState([]);
  const [currentSlide, setCurrentSlide] = useState(0);
  const [touchStart, setTouchStart] = useState(null);
  const [touchEnd, setTouchEnd] = useState(null);

  // Constants
  const CONTACT_NUMBER = "+251992011629";

  // Cleaned up and optimized mobile-first hero images (ensure these paths are correct in your /public/images folder)
  const heroImages = [
    { url: `${process.env.PUBLIC_URL}/images/fashion1.jpg` },
    { url: `${process.env.PUBLIC_URL}/images/fashion2.jpg` },
    { url: `${process.env.PUBLIC_URL}/images/fashion3.jpg` },
    { url: `${process.env.PUBLIC_URL}/images/fashion4.jpg` },
    { url: `${process.env.PUBLIC_URL}/images/fashion5.jpg` },
    { url: `${process.env.PUBLIC_URL}/images/fashion6.jpg` },
    { url: `${process.env.PUBLIC_URL}/images/fashion7.jpg` },
    { url: `${process.env.PUBLIC_URL}/images/fashion8.jpg` },
    { url: `${process.env.PUBLIC_URL}/images/fashion9.jpg` },
    { url: `${process.env.PUBLIC_URL}/images/fashion10.jpg` },
  ];

  // Touch handlers for better mobile swipe (existing logic preserved)
  const handleTouchStart = (e) => {
    setTouchStart(e.targetTouches[0].clientX);
  };

  const handleTouchMove = (e) => {
    setTouchEnd(e.targetTouches[0].clientX);
  };

  const handleTouchEnd = () => {
    if (!touchStart || !touchEnd) return;
    const distance = touchStart - touchEnd;
    const isLeftSwipe = distance > 50;
    const isRightSwipe = distance < -50;

    if (isLeftSwipe) {
      setCurrentSlide(prev => Math.min(prev + 1, heroImages.length - 1));
    }
    if (isRightSwipe) {
      setCurrentSlide(prev => Math.max(prev - 1, 0));
    }
    setTouchStart(null); // Reset touch state
    setTouchEnd(null);
  };

  // Load Products (Existing logic preserved)
  useEffect(() => {
    const loadProducts = () => {
      const data = localStorage.getItem("products");
      if (data) {
        try {
          const loaded = JSON.parse(data);
          setProducts(loaded);
          setFilteredProducts(loaded);

          const uniqueCategories = [
            ...new Set(
              loaded
                .map((p) => p.category?.trim())
                .filter(Boolean)
            ),
          ];
          setCategories(uniqueCategories);
        } catch (error) {
          console.error("Error loading products:", error);
        }
      }
    };

    loadProducts();
    // Use 'storage' event listener for real-time updates across tabs
    window.addEventListener("storage", loadProducts);
    return () => window.removeEventListener("storage", loadProducts);
  }, []);

  // Filter products (Existing logic preserved)
  useEffect(() => {
    if (selectedCategory === "all") {
      setFilteredProducts(products);
    } else {
      setFilteredProducts(
        products.filter(
          (p) =>
            p.category &&
            p.category.toLowerCase() === selectedCategory.toLowerCase()
        )
      );
    }
  }, [selectedCategory, products]);

  // Product Card Component with mobile optimization (Extracted for cleaner render)
  const ProductImage = ({ images, productName }) => {
    if (!images || images.length === 0) {
      return (
        <div className="aspect-square bg-gradient-to-br from-gray-50 to-gray-100 flex items-center justify-center rounded-xl">
          <svg
            className="w-10 h-10 text-gray-300"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth="1"
              d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z"
            />
          </svg>
        </div>
      );
    }

    return (
      <div className="aspect-square overflow-hidden rounded-xl bg-gray-50 relative">
        <img
          src={images[0]}
          alt={productName}
          className="w-full h-full object-cover transition-transform duration-300 hover:scale-105"
          loading="lazy"
          decoding="async"
          onError={(e) => {
            e.target.src = `https://via.placeholder.com/400/cccccc/ffffff?text=${encodeURIComponent(productName?.substring(0, 20) || 'Product')}`;
          }}
        />
      </div>
    );
  };
  
  // Product Card component with integrated WhatsApp button
  const ProductCard = ({ product }) => {
    // Generate pre-filled WhatsApp link
    const whatsAppLink = `https://wa.me/${CONTACT_NUMBER.replace('+', '')}?text=${encodeURIComponent(`Hello, I'm interested in the product: ${product.name} (ID: ${product.id}). Could you provide more details?`)}`;

    return (
        <div className="group bg-white border border-gray-100 rounded-xl overflow-hidden hover:shadow-xl transition-all duration-300 block relative">
            <Link to={`/product/${product.id}`} className="block">
                <ProductImage images={product.images} productName={product.name} />
            </Link>
            
            <div className="p-4 pt-3">
                <Link to={`/product/${product.id}`} className="block mb-3">
                    <h3 className="text-base font-semibold text-gray-900 line-clamp-2 mb-1 group-hover:text-yellow-500 transition-colors">
                        {product.name || 'Unnamed Product'}
                    </h3>
                    <p className="text-sm font-bold text-gray-800">
                        {(product.finalPrice || 0).toLocaleString()} ETB
                    </p>
                    <div className="mt-2 text-xs font-medium">
                        {product.status === "onhand" ? (
                            <span className="text-green-600 bg-green-50 px-2 py-0.5 rounded-full">In Stock</span>
                        ) : (
                            <span className="text-blue-600 bg-blue-50 px-2 py-0.5 rounded-full">By Order</span>
                        )}
                    </div>
                </Link>

                {/* WhatsApp Button on the product post */}
                <a
                    href={whatsAppLink}
                    target="_blank"
                    rel="noopener noreferrer"
                    onClick={(e) => e.stopPropagation()} // Prevents navigation to product details
                    className="w-full flex items-center justify-center gap-2 bg-green-500 hover:bg-green-600 text-white text-sm font-semibold py-2 rounded-lg transition-colors shadow-md"
                    aria-label={`Inquire about ${product.name} on WhatsApp`}
                >
                    <MessageSquare className="w-4 h-4" />
                    Inquire
                </a>
            </div>
        </div>
    );
  };

  return (
    <div className="min-h-screen flex flex-col bg-white text-gray-900">
      <Header />

      {/* HERO CAROUSEL - Mobile Optimized */}
      <section 
        className="relative h-[70vh] md:h-screen w-full overflow-hidden"
        onTouchStart={handleTouchStart}
        onTouchMove={handleTouchMove}
        onTouchEnd={handleTouchEnd}
      >
        <Carousel
          showThumbs={false}
          autoPlay
          infiniteLoop
          interval={5000}
          transitionTime={600}
          showStatus={false}
          showArrows={false}
          swipeable={true}
          emulateTouch={true}
          showIndicators={true}
          dynamicHeight={false}
          stopOnHover={false}
          useKeyboardArrows={true}
          onChange={setCurrentSlide}
          selectedItem={currentSlide}
          className="h-full"
          renderIndicator={(onClickHandler, isSelected, index) => (
            <button
              type="button"
              onClick={onClickHandler}
              onKeyDown={onClickHandler}
              value={index}
              key={index}
              role="button"
              tabIndex={0}
              aria-label={`Go to slide ${index + 1}`}
              className={`mx-1 h-2 rounded-full transition-all duration-300 ${
                isSelected ? 'w-8 bg-white shadow-md' : 'w-2 bg-gray-400/80'
              }`}
            />
          )}
        >
          {heroImages.map((img, index) => (
            <div key={index} className="relative h-[70vh] md:h-screen">
              <div className="absolute inset-0">
                {/* Dark overlay for text readability */}
                <div className="absolute inset-0 bg-black/40 z-10" />
                <img
                  src={img.url}
                  alt={`Fashion ${index + 1}`}
                  className="w-full h-full object-cover"
                  loading={index === 0 ? "eager" : "lazy"} // Eager load the first image
                />
              </div>

              {/* Content Overlay */}
              <div className="absolute inset-0 z-20 flex flex-col items-center justify-end pb-12 px-4 text-center">
                <div className="max-w-3xl">
                  {/* Updated Text: Removed "Premium Fashion Delivered to You" */}
                  <h1 className="text-4xl md:text-6xl font-extrabold text-white mb-3 tracking-tight">
                    Shop the latest trends from Etsub Online Boutique.
                  </h1>
                  {/* Removed the secondary text and the "Shop Now" button */}
                </div>
              </div>
            </div>
          ))}
        </Carousel>

        {/* Slide Indicator (Mobile-friendly placement) */}
        <div className="absolute bottom-6 right-4 z-30 hidden md:block">
          <div className="bg-black/40 backdrop-blur-sm px-3 py-1 rounded-full">
            <span className="text-white text-sm font-medium">
              {currentSlide + 1} / {heroImages.length}
            </span>
          </div>
        </div>
      </section>

      <main className="flex-grow">
        {/* CATEGORIES & FILTERS */}
        <section className="py-8 md:py-12 border-b border-gray-100">
          <div className="max-w-7xl mx-auto px-4 md:px-6">
            <h2 className="text-2xl font-bold text-gray-900 mb-6">Shop by Category</h2>
            <div className="flex flex-row overflow-x-auto pb-3 space-x-3 md:space-x-4 custom-scrollbar">
              {/* All button */}
              <button
                onClick={() => setSelectedCategory("all")}
                className={`flex-shrink-0 px-4 py-2 rounded-full font-medium text-sm transition-colors whitespace-nowrap ${
                  selectedCategory === "all" ? "bg-gray-900 text-white shadow-md" : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                }`}
              >
                All Products
              </button>

              {/* Dynamic Category Buttons */}
              {categories.map((category) => (
                <button
                  key={category}
                  onClick={() => setSelectedCategory(category)}
                  className={`flex-shrink-0 px-4 py-2 rounded-full font-medium text-sm transition-colors whitespace-nowrap ${
                    selectedCategory === category ? "bg-gray-900 text-white shadow-md" : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                  }`}
                >
                  {category}
                </button>
              ))}
            </div>
          </div>
        </section>

        {/* PRODUCT GRID */}
        <section id="product-grid" className="py-12 md:py-16">
          <div className="max-w-7xl mx-auto px-4 md:px-6">
            <h2 className="text-3xl font-bold text-gray-900 mb-8">
              {selectedCategory === "all" ? "Latest Arrivals" : `Category: ${selectedCategory}`}
            </h2>
            
            {filteredProducts.length === 0 ? (
              <div className="text-center py-16 bg-gray-50 rounded-xl border border-gray-100">
                <div className="w-20 h-20 mx-auto mb-4 rounded-full bg-gray-100 flex items-center justify-center">
                  <svg className="w-10 h-10 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="1.5" d="M20 13V6a2 2 0 00-2-2H6a2 2 0 00-2 2v7m16 0v5a2 2 0 01-2 2H6a2 2 0 01-2-2v-5m16 0h-2.586a1 1 0 00-.707.293l-2.414 2.414a1 1 0 01-.707.293h-3.172a1 1 0 01-.707-.293l-2.414-2.414A1 1 0 006.586 13H4" />
                  </svg>
                </div>
                <h3 className="text-xl font-medium text-gray-900 mb-2">No products found</h3>
                <p className="text-gray-500 text-base">
                  {selectedCategory === "all" ? "Check back soon for new arrivals." : "Try another category or 'All Products'."}
                </p>
              </div>
            ) : (
              // Mobile-first responsive grid
              <div className="grid grid-cols-2 sm:grid-cols-3 lg:grid-cols-4 gap-4 md:gap-6">
                {filteredProducts.map((product) => (
                  <ProductCard key={product.id} product={product} />
                ))}
              </div>
            )}
          </div>
        </section>

        {/* Removed the 'Need Assistance?' WhatsApp CTA section as requested */}

      </main>

      <Footer />
    </div>
  );
}