// src/components/Header.jsx

import { Link } from "react-router-dom";
import { useState } from "react";
import { Menu, X, Phone } from "lucide-react"; // Importing icons for cleaner code

export default function Header() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const CONTACT_NUMBER = "+251992011629";

  // Using PUBLIC_URL to correctly reference assets
  const logo192 = `${process.env.PUBLIC_URL}/logo192.png`;

  const toggleMenu = () => setIsMenuOpen(!isMenuOpen);

  // Apply the requested Crisp White theme
  return (
    <header className="sticky top-0 z-[999] bg-white shadow-lg border-b border-gray-100">
      <div className="max-w-7xl mx-auto flex justify-between items-center p-4 md:p-6">
        {/* Logo + Brand */}
        <Link to="/" className="flex items-center gap-3 z-[1001] transition-opacity hover:opacity-90">
          <img
            src={logo192}
            alt="ETSUB SHOP Logo"
            className="w-10 h-10 md:w-12 md:h-12 object-contain rounded-full border-2 border-gray-200"
          />
          <div className="leading-tight">
            {/* Text color changed to black for white background */}
            <h1 className="text-xl md:text-2xl font-black tracking-tight text-gray-900">
              ETSUB <span className="text-yellow-500">SHOP</span>
            </h1>
            <p className="text-xs text-gray-500 -mt-0.5 font-medium hidden md:block">
              Online Fashion Boutique
            </p>
          </div>
        </Link>

        {/* Desktop Menu */}
        <nav className="hidden md:flex items-center gap-8 z-[1001]">
          {/* Link colors updated for better contrast on white background */}
          <Link
            to="/"
            className="text-gray-700 font-medium hover:text-black transition-colors"
          >
            Home
          </Link>
          <Link
            to="/admin"
            className="text-gray-700 font-medium hover:text-black transition-colors"
          >
            Admin
          </Link>
          <a
            href={`tel:${CONTACT_NUMBER}`}
            className="flex items-center gap-2 px-4 py-2 bg-yellow-500 text-gray-900 rounded-full font-semibold hover:bg-yellow-600 transition-colors shadow-md"
          >
            <Phone className="w-4 h-4" />
            Call Now
          </a>
        </nav>

        {/* Mobile Menu Button */}
        <button
          onClick={toggleMenu}
          className="md:hidden p-2 text-gray-800 rounded-lg hover:bg-gray-100 transition-colors z-[1001]"
          aria-label="Toggle menu"
        >
          {isMenuOpen ? <X className="w-6 h-6" /> : <Menu className="w-6 h-6" />}
        </button>
      </div>

      {/* Mobile Menu Panel */}
      <div
        className={`md:hidden fixed inset-x-0 top-[72px] bg-white p-6 shadow-xl z-[1000] transition-all duration-300 ${
          isMenuOpen ? "translate-y-0 opacity-100 visible" : "-translate-y-full opacity-0 invisible"
        }`}
      >
        <nav className="flex flex-col gap-1">
          <Link
            to="/"
            onClick={() => setIsMenuOpen(false)}
            className="py-3 text-lg text-gray-800 font-medium hover:text-black hover:bg-gray-50 rounded-lg text-center transition-colors border-b border-gray-100"
          >
            Home
          </Link>

          <Link
            to="/admin"
            onClick={() => setIsMenuOpen(false)}
            className="py-3 text-lg text-gray-800 font-medium hover:text-black hover:bg-gray-50 rounded-lg text-center transition-colors border-b border-gray-100"
          >
            Admin
          </Link>

          <a
            href={`tel:${CONTACT_NUMBER}`}
            onClick={() => setIsMenuOpen(false)}
            className="mt-2 py-3 text-lg text-white font-medium bg-yellow-500 hover:bg-yellow-600 rounded-lg text-center transition-colors shadow-md flex items-center justify-center gap-2"
          >
            <Phone className="w-5 h-5" />
            Call Us
          </a>
        </nav>
      </div>
    </header>
  );
}